body {
  margin: 10px;
  padding: 0;
  font-family: sans-serif;
  font-size: 16pt;
}

.ClusterApp {
  width: 1200px;
  background-color: #CECECE;
  margin: 0 auto;
  border: 1px solid black;
}

input[type=checkbox] {
  -webkit-transform: scale(2.0);
      -ms-transform: scale(2.0);
          transform: scale(2.0);
}

.SideAndPlot {

  display: -ms-flexbox;

  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: stretch;
      align-items: stretch;
  margin: 0;
  padding: 0;
}

.SideBar {
  background-color: #CECECE;
  width: 500px;
  padding: 0 10px;
}

.PlotRegion{
  background-color: white;
}

.PlotRegion:after{
  content:"";
  display: table;
  clear:both;
}

.StepBar{
  background-color: #CECECE;
  margin: 0;
  padding: 0;
}

.NumericSlider {
  border: 1px solid black;
  margin: 15px 5px;
}

.SliderControls {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  width: 100%;
}

.SliderControls .Slider {
  -ms-flex-positive: 3;
      flex-grow: 3;
  -ms-flex-negative: 3;
      flex-shrink: 3;
}

.SliderControls .TextInput {
  width: 6ex;
}

.NeighborhoodControl {
  border: 1px solid black;
  padding: 10px;
  margin: 20px 5px;
}

.NeighborhoodButtonContainer{
  display: -ms-flexbox;
  display: flex;
  margin-top: 20px;
  -ms-flex-pack:justify;
      justify-content:space-between;
}

button {
  width: 45%;
  font-size: 14pt;
}



